import { v4 as uuid4 } from 'uuid';

export const addExpense = (
    {
        description = '', 
        type = '', 
        note = '', 
        amount = 0,
        createdAt = 0
    } = {}
) => ({
    type: 'ADD_EXPENSE',
    expense: {
        id: uuid4(),
        description,
        note,
        type,
        amount,
        createdAt
    }
})

export const removeExpense = ({ id } = {}
) => ({
    type: 'REMOVE_EXPENSE',
    id
})

export const editExpense = ( id, updates) => ({
    type: 'EDIT_EXPENSE',
    id,
    updates
})




