import React from 'react'
import { Link } from 'react-router-dom'



const ExpenseListItem = ({ id, description, type, amount, createdAt }) => (
    <div>
        <Link to={`/edit/${id}`}>
    <h3>{description}</h3>
    </Link>
        <p>{type}</p>
        <p>{amount} - {createdAt}</p>
    </div>
)

export default ExpenseListItem


